import React from 'react';
import logo from './logo.svg';
import AddTodo from './Compo/AddTodo';
import TodoList from './Compo/TodoList';
// import Clock from './Compo/Clock';
// import OnOffButton from './Compo/OnOffButton';
import TestingCompo from './Compo/TestingCompo';
// import AddTodo, { TodoList, TestingCompo } from './Compo/*';
import './App.css';

class MyApp extends React.Component{
  constructor(){
    super();
    this.state = {
      todos: [] 
    }
  }

  render(){
    return(
      <div className="App">
        <h1>Hello world!</h1>
        <AddTodo addTodoFn={this.addTodo}></AddTodo>
        <TodoList todos={this.state.todos}></TodoList>
        <TestingCompo></TestingCompo>
      </div>
    )  
  }

  componentDidMount = () => {
    const todos = localStorage.getItem('todos');
    if (todos){
      const savedTodos = JSON.parse(todos);
      this.setState({ todos : savedTodos })
      console.log('yes todos', todos);
    }else{
      console.log('no todos', this.state);
    }
  }

  addTodo = async  (todo) => {
    // !! setState 는 비동기적으로 실행됨 -> async 와 await를 사용
    await this.setState({todos : [...this.state.todos, todo]});   // wtf?
    localStorage.setItem('todos', JSON.stringify(this.state.todos));  // state의 todos를 JSON.stringfy한뒤에 로컬저장소에 저장(setItem('todos'))
    console.log(localStorage.getItem('todos'));

  }
  
  // addTodo = (todo) => console.log(todo);
  

}

export default MyApp;
